import times from 'lodash/times'
import { createSlice } from '@reduxjs/toolkit'
import { layerIndexChanged, makeLayerIndexChangeReducer } from './layer-index-change-action'

const DEFAULT_BIND_BEHAVIOUR = '&trans'
const createEmptyBinding = (value = DEFAULT_BIND_BEHAVIOUR) => ({
  value, params: []
})

export const selectLayers = state => state.keyboard.keymap.layers

const layersSlice = createSlice({
  name: 'layers',
  initialState: [],
  reducers: {
    createLayer (state, action) {
      state.push({
        name: `layer_${state.length}`,
        labels: [],
        properties: {
          bindings: {
            parsed: times(action.payload.bindingCount, () => createEmptyBinding())
          }
        }
      })
    },
    updateLayerMetadata (state, action) {
      const { layerIndex, name, label } = action.payload
      if (name) {
        state[layerIndex].name = name
      }
      if (label !== undefined) {
        state[layerIndex].properties.label = {
          parsed: label
        }
      }
    },
    renameLayer (state, action) {
      state[action.payload.index].name = action.payload.name
    },
    repositionLayer (state, action) {
      const { currentIndex, targetIndex } = action.payload
      if (currentIndex === targetIndex) {
        return
      }

      const layer = state[currentIndex]

      // moving a layer up means shifting other layers down
      const replacementIndex = targetIndex > currentIndex
        ? targetIndex - 1
        : targetIndex

      state.splice(currentIndex, 1)
      state.splice(replacementIndex, 0, layer)
    },
    deleteLayer (state, action) {
      state.splice(action.payload.index, 1)
    },
    updateKeyBinding (state, action) {
      state[action.payload.layerIndex].properties.bindings.parsed[action.payload.bindIndex] = action.payload.binding
    },
    updateSensorBindings (state, action) {
      state[action.payload.layerIndex].properties['sensor-bindings'].parsed[action.payload.sensorIndex] = action.payload.bindings
    },
    rebindTransparentToKeyPress (state, action) {
      for (const binding of state[action.payload.layerIndex].properties.bindings.parsed) {
        if (binding.value === '&trans') {
          binding.value = '&kp'
          binding.params = []
        }
      }
    },
    rebindTransparentToNone (state, action) {
      for (const binding of state[action.payload.layerIndex].properties.bindings.parsed) {
        if (binding.value === '&trans') {
          binding.value = '&none'
          binding.params = []
        }
      }
    },
    rebindNoneToKeypress (state, action) {
      for (const binding of state[action.payload.layerIndex].properties.bindings.parsed) {
        if (binding.value === '&none') {
          binding.value = '&kp'
          binding.params = []
        }
      }
    },
    rebindNoneToTransparent (state, action) {
      for (const binding of state[action.payload.layerIndex].properties.bindings.parsed) {
        if (binding.value === '&none') {
          binding.value = '&trans'
          binding.params = []
        }
      }
    },
    rebindEmptyKeypressToTransparent (state, action) {
      for (const binding of state[action.payload.layerIndex].properties.bindings.parsed) {
        if (binding.value === '&kp' && !binding.params[0]?.value) {
          binding.value = '&trans'
          binding.params = []
        }
      }
    },
    rebindEmptyKeypressToNone (state, action) {
      for (const binding of state[action.payload.layerIndex].properties.bindings.parsed) {
        if (binding.value === '&kp' && !binding.params[0]?.value) {
          binding.value = '&none'
          binding.params = []
        }
      }
    }
  },
  extraReducers: builder => {
    builder.addCase(
      layerIndexChanged,
      makeLayerIndexChangeReducer()
    )
  }
})

export const {
  createLayer,
  renameLayer,
  repositionLayer,
  deleteLayer,
  updateLayerMetadata,
  updateKeyBinding,
  updateSensorBindings,
  rebindTransparentToKeyPress,
  rebindTransparentToNone,
  rebindNoneToKeypress,
  rebindNoneToTransparent,
  rebindEmptyKeypressToTransparent,
  rebindEmptyKeypressToNone
} = layersSlice.actions

export default layersSlice.reducer
