import omit from 'lodash/omit'
import { createSlice } from '@reduxjs/toolkit'
import { resolveBehaviorParams } from 'zmk-data/parameters'
import { layerIndexChanged, makeLayerIndexChangeReducer } from '../Layers/layer-index-change-action'

export const selectBehaviors = state => state.keyboard.keymap.behaviors

const selectNodeCompatible = node => (
  node.properties.compatible?.parsed ||
  node.properties.compatible?.raw?.replace(/"/g, '')
)

const selectNodePhandleArrays = (node, behaviors, schemas) => {
  const compatible = selectNodeCompatible(node)
  const schema = schemas[compatible]

  if (!schema) {
    return []
  }

  return Object.keys(schema.properties).reduce((phandleArrays, prop) => {
    const propSchema = schema.properties[prop]
    const propValues = node.properties[prop]
    if (propSchema.type === 'phandle-array' && propValues) {
      phandleArrays.push(propValues.parsed)
    }

    return phandleArrays
  }, [])
}

const behaviorsSlice = createSlice({
  name: 'behaviors',
  initialState: [],
  reducers: {
    addBehavior (state, action) {
      state.push(action.payload.behavior)
    },
    updateBehavior (state, action) {
      state[action.payload.behaviorIndex] = omit(action.payload.behavior, 'params')
      resolveBehaviorParams(state)
    },
    deleteBehavior (state, action) {
      state.splice(action.payload.behaviorIndex, 1)
    }
  },
  extraReducers: builder => {
    builder.addCase(
      layerIndexChanged,
      makeLayerIndexChangeReducer(
        selectNodePhandleArrays
      )
    )
  }
})

export const {
  addBehavior,
  updateBehavior,
  deleteBehavior
} = behaviorsSlice.actions

export default behaviorsSlice.reducer
