import PropTypes from 'prop-types'
import { binding } from '../bindingPropTypes'

export const combo = PropTypes.shape({
  index: PropTypes.number,
  name: PropTypes.string,
  properties: PropTypes.shape({
    bindings: PropTypes.shape({
      parsed: PropTypes.arrayOf(binding).isRequired
    }).isRequired,
    'key-positions': PropTypes.shape({
      parsed: PropTypes.arrayOf(PropTypes.number).isRequired
    }).isRequired
  })
})
