import * as config from '../../config'

export async function loadAvailableKeyboards () {
  const response = await fetch(`${config.apiBaseUrl}/v2/local/keyboards`)
  return response.json()
}

export async function loadKeyboardFiles (keyboard) {
  const path = `v2/local/keyboards/${encodeURIComponent(keyboard)}`
  const url = `${config.apiBaseUrl}/${path}`
  const response = await fetch(url)

  if (!response.ok) {
    const err = new Error()
    err.response = response
    throw err
  }
  return response.json()
}

export async function updateKeymap (keyboard, keymap) {
  const path = `v2/local/keyboards/${encodeURIComponent(keyboard)}/keymap`
  const url = `${config.apiBaseUrl}/${path}`

  const response = await fetch(url, {
    method: 'PUT',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify(keymap)
  })

  return response.json()
}
