import { createSlice } from '@reduxjs/toolkit'
import { layerIndexChanged, makeLayerIndexChangeReducer } from '../Layers/layer-index-change-action'

export const selectCombos = state => state.keyboard.keymap.combos

const combosSlice = createSlice({
  name: 'combos',
  initialState: [],
  reducers: {
    updateComboBinding (state, action) {
      state[action.payload.comboIndex].properties.bindings = { parsed: action.payload.bindings }
    },
    updateComboKeypositions (state, action) {
      state[action.payload.comboIndex].properties['key-positions'] = {
        parsed: action.payload.keyPositions
      }
    },
    createCombo (state) {
      state.push({
        name: 'new_combo',
        labels: [],
        properties: {
          bindings: {
            parsed: [{ value: '&trans', params: [] }]
          },
          'key-positions': {
            parsed: []
          }
        }
      })
    },
    renameCombo (state, action) {
      state[action.payload.index].name = action.payload.name
    },
    deleteCombo (state, action) {
      state.splice(action.payload.index, 1)
    },
    customizeCombo (state, action) {
      const { index, combo } = action.payload
      state[index] = combo
    }
  },
  extraReducers: builder => {
    const comboBindingsReducer = makeLayerIndexChangeReducer()

    builder.addCase(
      layerIndexChanged,
      function reducer (state, action) {
        const { positionMapping } = action.payload
        comboBindingsReducer(state, action)
        for (const { properties } of state) {
          if (!properties.layers?.parsed?.length) {
            continue
          }

          properties.layers.parsed = properties.layers.parsed.reduce((layers, index) => {
            const newIndex = positionMapping[index]
            if (newIndex !== null) {
              layers.push(newIndex)
            }

            return layers
          }, [])
        }
      }
    )
  }
})

export const {
  updateComboBinding,
  updateComboKeypositions,
  createCombo,
  renameCombo,
  deleteCombo,
  customizeCombo
} = combosSlice.actions

export default combosSlice.reducer
