import get from 'lodash/get'
import { createAction } from '@reduxjs/toolkit'

import { getBehaviourParams } from '../../keymap'

export function selectLayerParamsFromBinding (binding, behaviors) {
  const { value, params } = binding
  const behavior = get(behaviors, value, {})
  const behaviorParams = getBehaviourParams(params, behavior)

  return behaviorParams.reduce((layerParams, param, i) => {
    if (param === 'layer') {
      layerParams.push(params[i])
    }

    return layerParams
  }, [])
}

export const layerIndexChanged = createAction(
  'layers/indexChanged',
  function prepare (currentIndex, targetIndex, layers, sources, behaviorSchemas) {
    const positionMapping = createLayerIndexMapping(currentIndex, targetIndex, layers)

    return {
      payload: {
        currentIndex,
        targetIndex,
        sources,
        behaviorSchemas,
        positionMapping
      }
    }
  }
)

const defaultSelectPhandleArrays = node => [node.properties.bindings.parsed]

/**
 * Create a reducer to update layer index binding cells after a layer shift.
 * @param {Function} selectPhandleArrays - callback to return parsed phandle-array values from any relevant property in a node
 * @returns {Function} reducer
 */
export function makeLayerIndexChangeReducer (selectPhandleArrays = defaultSelectPhandleArrays) {
  return function reducer (state, action) {
    const { sources, behaviorSchemas, positionMapping } = action.payload
    const { behaviours: behaviors, defines } = sources
    const layerParamsIterator = makeLayerParamsIterator(
      state, behaviors, behaviorSchemas, selectPhandleArrays
    )

    for (const param of layerParamsIterator) {
      let selectedIndex = get(defines, param.value, param.value)
      if (!Number.isNaN(Number(selectedIndex))) {
        selectedIndex = Number(selectedIndex)
      }

      param.value = positionMapping[selectedIndex]?.toString()
      if (param.value === undefined) {
        param.value = '0'
        param._isPlaceholder = true
      }
    }
  }
}

function createLayerIndexMapping (currentIndex, targetIndex, layers) {
  const layerIndices = [...layers.keys()]

  // Deleted layer means references to currentIndex are nulled,
  // and referencs > currentIndex are decremented
  if (targetIndex === null) {
    return layerIndices.map(i => {
      if (i === currentIndex) {
        return null
      }

      return i > currentIndex ? i - 1 : i
    })
  }

  return layerIndices.map(i => {
    const sameIndex = i === currentIndex
    const bothBefore = i > currentIndex && i > targetIndex
    const bothAfter = i < currentIndex && i < targetIndex
    const betweenOldAndNew = currentIndex < i && i < targetIndex

    if (sameIndex) {
      // Normally the "target" is another layer whose position is being taken,
      // but in the case of targetting the end of the list this isn't a valid
      // array index.
      if (targetIndex === layers.length) {
        return targetIndex - 1
      }

      return targetIndex
    } else if (bothBefore || bothAfter) {
      return i
    } else if (betweenOldAndNew) {
      return i - 1
    }

    return i + 1
  })
}

/**
 * Create iterator for all layer-related binding cells in a state slice.
 * This reducer is used with state slices that represent a collection of nodes
 * each of which have one or more properties representing phandle arrays which
 * may have parameters binding to layer indices. This iterator abstracts that
 * so we can just loop through layer parameters.
 * @param {Array<Object>} state
 * @param {*} behaviors
 * @param {*} behaviorSchemas
 * @param {Function} selectPhandleArrays
 */
function * makeLayerParamsIterator (state, behaviors, behaviorSchemas, selectPhandleArrays) {
  for (const node of state) {
    for (const bindings of selectPhandleArrays(node, behaviors, behaviorSchemas)) {
      for (const binding of bindings) {
        for (const param of selectLayerParamsFromBinding(binding, behaviors)) {
          yield param
        }
      }
    }
  }
}
