import PropTypes from 'prop-types'

import { apiBaseUrl } from '../config'
import Link from '../Common/Link'
import Modal from '../Common/Modal'
import DialogBox from '../Common/DialogBox'
import IconButton from '../Common/IconButton'

import styles from './unexpected-error.module.css'
import { useEffect, useState } from 'react'

function formatStackFrame (frame) {
  return `  ${frame.fileName}:${frame.lineNumber}:${frame.columnNumber}`
}

function formatErrorText (error, stack = []) {
  return `
${error.name}

${error.message}

${stack.map(formatStackFrame).join('\n')}
`.trim()
}

const newGithubIssue = (
  <Link external href="https://github.com/nickcoutsos/keymap-editor/issues/new">
    new issue
  </Link>
)

async function reportError (error, stacktrace) {
  const url = `${apiBaseUrl}/v2/events/client-errors`
  const body = JSON.stringify({
    error: error.toString(),
    stacktrace
  })

  const response = await fetch(url, {
    method: 'POST',
    headers: { 'Content-Type': 'application/json' },
    body
  })

  if (!response.ok) {
    console.error('Failed to submit error report', response)
  }
}

function UnexpectedError ({ error }) {
  const [stack, setStack] = useState([])
  const [formattedError, setFormattedError] = useState('')
  const [copied, setCopied] = useState(false)

  useEffect(() => {
    console.log('effect for error', error)
    async function parseStack () {
      const StackTrace = await import('stacktrace-js')
      const stacktrace = await StackTrace.fromError(error)
      setStack(stacktrace)
      reportError(error, stacktrace)
    }

    parseStack()
  }, [setStack, error])

  useEffect(() => {
    setFormattedError(formatErrorText(error, stack))
  }, [error, stack])

  useEffect(() => {
    if (copied) {
      setTimeout(() => {
        setCopied(false)
      }, 3000)
    }
  }, [copied, setCopied])

  function copyErrorText () {
    const backTicks = '```'
    const text = `${backTicks}\n${formattedError}\n${backTicks}`
    navigator.clipboard.writeText(text)
      .then(() => setCopied(true))
  }

  return (
    <Modal>
      <DialogBox dismissText={false} style={{ maxWidth: '700px' }}>
        <h2>Unexpected error</h2>
        <p>
          Something unexpected happened and the keymap editor app cannot
          recover. Please consider taking a moment to open a {newGithubIssue}
          with the details below to help me investigate and improve the app.
        </p>
        <div className={styles.stackTrace}>
          <pre>{formattedError}</pre>
          <IconButton
            icon="copy"
            title="Copy error text"
            className={styles.button}
            onClick={copyErrorText}
          />
          {copied && (
            <div className={styles.copyMessage}>Copied!</div>
          )}
        </div>
      </DialogBox>
    </Modal>
  )
}

UnexpectedError.propTypes = {
  error: PropTypes.instanceOf(Error).isRequired
}

export default UnexpectedError
