import PropTypes from 'prop-types'
import { binding } from './bindingPropTypes'
import { combo } from './Combos/comboPropTypes'

export const layer = PropTypes.shape({
  index: PropTypes.number,
  name: PropTypes.string,
  properties: PropTypes.shape({
    bindings: PropTypes.shape({
      parsed: PropTypes.arrayOf(binding).isRequired
    }).isRequired,
    'sensor-bindings': PropTypes.shape({
      parsed: PropTypes.arrayOf(binding).isRequired
    })
  })
})

export const keymap = PropTypes.shape({
  layers: PropTypes.arrayOf(layer).isRequired,
  combos: PropTypes.arrayOf(combo)
})
